import React from 'react'

function Pegasus() {
  return (
    <div className="container w-full md:max-w-4xl mx-auto pt-2">
        <div className="w-full px-4 md:px-6 text-xl text-gray-800 leading-normal">
        <div className="jsx-1794874986 font-sans">
            <h1 className="jsx-1794874986 font-bold font-sans break-normal text-gray-900 pt-6 pb-2 text-3xl md:text-4xl">Everything About Pegasus Spyware</h1>
            </div>
        </div>
        <div className="py-6 break-words">
        <h4><span style={{fontWeight: "400"}}>What is Pegasus?</span></h4>
        <br />
        <p><span style={{fontWeight: "400"}}>Pegasus is a spyware developed by the Israeli cyber arms firm NSO Group that can be installed on mobile phones running iOS, Android &amp; Blackberry. Pegasus is capable of reading text messages, tracking calls, collecting passwords, location tracking, accessing the target device's microphone and camera, and harvesting information from apps. This is a Trojan horse that can be sent "flying through the air" to infect phones.</span></p>
        <br />
        <h4><span style={{fontWeight: "400"}}>How Pegasus Infects:</span></h4>
        <br />
        <p><span style={{fontWeight: "400"}}>Pegasus uses Zero Click Attack. The older version of the spyware(2016-2018) used the SPEAR PHISHING technique to get into a device. Spear Phishing is a phishing technique where you click on one link and it installs the software on your device. But now, it can reach other phones using ZERO-CLICK-ATTACK which is as scary as it sounds. In Zero-Click-Attack, the spyware exploits a vulnerability of one of the apps presented on the phone without any interaction on your side, and then it spreads itself to the other device without the users knowledge.</span></p>
        <br />
        <h4><span style={{fontWeight: "400"}}><div className="cwhads"><ins className="adsbygoogle" style={{display:"block"}} data-ad-client="ca-pub-9655830461045889" data-ad-slot="8999979664" data-ad-format="auto" data-full-width-responsive="true"></ins></div></span></h4>
        <br />
        <h4><span style={{fontWeight: "400"}}>TRIDENT Vulnerability in 2016:</span></h4>
        <br />
        <p><span style={{fontWeight: "400"}}>In 2016 a security analysis shows that the malware exploits three zero-day vulnerabilities, </span><strong>Trident</strong><span style={{fontWeight: "400"}}>, in Apples iOS:</span></p>
        <br />
        <ol>
<li><span style={{fontWeight: "400"}}> </span><strong>CVE-2016-4657</strong><span style={{fontWeight: "400"}}>: Memory Corruption in WebKit - A vulnerability in Safari WebKit allows the attacker to compromise the device when the user clicks on a link.</span></li>
<li><span style={{fontWeight: "400"}}> </span><strong>CVE-2016-4655</strong><span style={{fontWeight: "400"}}>: Kernel Information Leak - A kernel base mapping vulnerability that leaks information to the attacker that allows him to calculate the kernels location in memory.</span></li>
<li><span style={{fontWeight: "400"}}> </span><strong>CVE-2016-4656</strong><span style={{fontWeight: "400"}}>: Kernel Memory corruption leads to Jailbreak - 32 and 64-bit iOS kernel-level vulnerabilities that allow the attacker to silently jailbreak the device and install surveillance software.</span></li>
</ol>
<br />
<p><span style={{fontWeight: "400"}}>These vulnerabilities were present in the versions of iOS up to iOS 9.3.4. Given the severity of Trident, Apple worked extremely quickly to patch these vulnerabilities and has released iOS 9.3.5 to address them.&nbsp;</span></p>
<br />
<h4><span style={{fontWeight: "400"}}>Whatsapp Call Vulnerability in 2019:</span></h4>
<br />
<p><span style={{fontWeight: "400"}}>In 2019 WhatsApp revealed that NSOs software had been used to send malware to more than 1,400 phones by exploiting a zero-day vulnerability. Simply by placing a WhatsApp call to a target device, malicious Pegasus code could be installed on the phone, even if the target never answered the call.</span></p>
<br />
<p><span style={{fontWeight: "400"}}>Now the Security Issue has been fixed by Facebook the parent company of Whatsapp.&nbsp;</span></p>
<br />
<p><a href="https://www.facebook.com/security/advisories/cve-2019-3568"><span style={{fontWeight: "400"}}>https://www.facebook.com/security/advisories/cve-2019-3568</span></a></p>
<br />
<h4><span style={{fontWeight: "400"}}>What does Pegasus do after getting inside your phone?</span></h4>
<br />
<p><span style={{fontWeight: "400"}}>Once the spyware gets to the phone it can gain root access easily and pretty much do anything. Like it can look into your photos, videos, call logs, all the information stored in the device. And the scariest part is, it can start your camera and microphone anytime. It can also read your keystrokes. So if you are typing your password, then the spyware can read it too. It hooks into widely used secure messenger applications to copy cleartext data out of them before the users app can encrypt and send it. From the perspective of the user and the people theyre communicating with, their communications are secure, while the administrator of the </span><span style={{fontWeight: "400"}}>Pegasus instance has secretly intercepted the clear text of their communication.</span></p>
<br />
<p><img src="https://api.codewithharry.com/media/blogFiles/pegasus-spyware-2021/PegaSUS.jpg"/></p>
<br />
<h4><span style={{fontWeight: "400"}}><div class="cwhads"><ins class="adsbygoogle" style={{display:"block"}}data-ad-client="ca-pub-9655830461045889" data-ad-slot="8999979664" data-ad-format="auto" data-full-width-responsive="true"></ins></div></span></h4>
<br />
<h4><span style={{fontWeight: "400"}}>What to do?</span></h4>
<br />
<p><span style={{fontWeight: "400"}}>If you are just a normal person, then I think you are safe from it because right now only some high profile people are being targeted. It does not mean that we can take this lightly. The spyware violates several Indian IT Rules and the government is taking strict action against it.&nbsp;</span></p>
<br />
<h4><span style={{fontWeight: "400"}}>Where can I download it?</span></h4>
<br />
<p><span style={{fontWeight: "400"}}>I have seen people googling "pegasus apk download". It does not work like that. You will have to buy one license for it. Pegasus carries a high price tag averaging at over $25,000 per target. And the process is highly complicated for a normal person to buy. So, if you see pegasus.apk on the internet there is a 100% chance that it is fake.&nbsp;</span></p>
<br />
<h4><span style={{fontWeight: "400"}}>The Conclusion:</span></h4>
<br />
<p><span style={{fontWeight: "400"}}>As of now several thousands of people are infected worldwide by this spyware. How many and which Zero Day Exploits does pegasus use to infect is still unknown to us. Right now all we can say is that if you are not in connection with any high-profile people who have recently been infected then you are probably safe. </span></p>
<br />
        </div>
    </div>
  )
}

export default Pegasus